import React from 'react';
import PropTypes from 'prop-types';
import './ListOfBusines.styles.css';

const ListOfBusines = (props) => {

    // let color_status = null
    // if (props.todos.completed) {
    //     color_status = `${green}`
    // } else {
    //     color_status = `${red}`
    // }
    

    return (
        <ul className='listOfBusines'>
            {props.todos.map((todo, index) =>
                <li key = {todo.id}>
                    <img alt ={`image-${todo.id}`} src={`https://robohash.org/${todo.id}?set=set3&size=64x64`}/>
                    <span>{index + 1}. </span>
                    {todo.title}
                    <input type="checkbox" onChange = {()=>props.onChangeres(todo.id)}/>
                    <button>&times;</button>
                </li>)}
        </ul>
    )
}
ListOfBusines.propTypes = {
    todos: PropTypes.arrayOf(PropTypes.object).isRequired,
    index: PropTypes.number,
    todo: PropTypes.string
}


export default ListOfBusines
