import React, { Component } from 'react';
import '../src/components/header/Header.module.css'
import { Header } from './components/header/Header';
import Main from './components/main/Main/Main';
import { SearchBox } from './components/search-box/search-box.component';
class App extends Component {


    constructor(props) {
        super(props);

        this.state = {
            todos: [
                //Не нужно так как мы подключили пока что API 
                //Свой стейт не используем
                // {
                //     id: 1,
                //     title: "Проснуться в 7:00",
                //     completed: true
                // },
                // {
                //     id: 2,
                //     title: "Почистить зубы",
                //     completed: true
                // },
                // {
                //     id: 3,
                //     title: "Одеться в школу",
                //     completed: false
                // }
            ],
            searchField: ''
        };
        
        
        //bind мы используем если будет в дальшейшем создавать функцию а не стрелочную. Если мы создадим обработчик событий через стрелочную функцию нам не нужно её биндить

        //this.onSearchValue = this.onSearchValue.bind(this)
    }

    // toggleTodoHandler(id) {
    //     this.state.todos = this.state.todos.map ( todo => {
    //         if (todo.id === id ) {
    //             todo.completed = !todo.completed
    //         }
    //         return todo
    //     })
    // }

    componentDidMount() {
        const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const url = "https://jsonplaceholder.typicode.com/todos/"; // site that doesn’t send Access-Control-*
        fetch(proxyurl + url) // https://cors-anywhere.herokuapp.com/https://example.com
            .then(res => res.json())
            .then(todo => { this.setState({ todos: todo }) })
            .catch(() => console.log("Can’t access " + url + " response. Blocked by browser?"))
    }

    //если будет просто функцию то её нужно биндить в стейте
    onSearchValue = (e) => {
       this.setState({ searchField: e.target.value})
    }
    

    render() {
        const {searchField, todos} = this.state;
        const filteringTodo = todos.filter( todo => todo.title.toLowerCase().includes(searchField.toLowerCase())
        )

        return (
            <div>
                <Header />
                <SearchBox
                    placeholder="Найти"
                    onSearch={this.onSearchValue} //после value через запятую можно console.log сделать this.state
                />
                <Main
                    todos={filteringTodo}
                    // onChancher={this.toggleTodoHandler}
                />
            </div>
        )
    }
}


export default App








